import React from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"

const Consulting = ({ data }) => (
  <Layout>
    <div className="page consulting">
      <SEO title="Consulting" />
      <h1>{data.allStrapiPageConsulting.nodes[0].titre}</h1>
      <div className="page__content">
        <p className="page__content__main">{data.allStrapiPageConsulting.nodes[0].description}</p>
        <p className="page__content__margin">{data.allStrapiPageConsulting.nodes[0].information}</p>
      </div>
    </div>
  </Layout>
)

export default Consulting

export const pageQuery = graphql`
  query ConsultingPageQuery {
    allStrapiPageConsulting {
        nodes {
          information
          description
          titre
        }
    }
  }
`